const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001/api'

interface ApiResponse<T = any> {
  success: boolean
  message: string
  data?: T
}

class ApiClient {
  private baseURL: string
  private token: string | null = null

  constructor(baseURL: string) {
    this.baseURL = baseURL
    
    // Get token from localStorage if available
    if (typeof window !== 'undefined') {
      this.token = localStorage.getItem('speedchallenge_token')
    }
  }

  private async request<T>(
    endpoint: string,
    options: RequestInit = {}
  ): Promise<ApiResponse<T>> {
    const url = `${this.baseURL}${endpoint}`
    
    const config: RequestInit = {
      headers: {
        'Content-Type': 'application/json',
        ...(this.token && { Authorization: `Bearer ${this.token}` }),
        ...options.headers,
      },
      ...options,
    }

    try {
      const response = await fetch(url, config)
      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.message || `HTTP error! status: ${response.status}`)
      }

      return data
    } catch (error) {
      console.error('API request failed:', error)
      throw error
    }
  }

  // Auth methods
  async register(userData: {
    email: string
    password: string
    name: string
  }): Promise<ApiResponse<{ user: any; token: string }>> {
    const response = await this.request<{ user: any; token: string }>('/auth/register', {
      method: 'POST',
      body: JSON.stringify(userData),
    })

    if (response.success && response.data?.token) {
      this.setToken(response.data.token)
    }

    return response
  }

  async login(credentials: {
    email: string
    password: string
  }): Promise<ApiResponse<{ user: any; token: string }>> {
    const response = await this.request<{ user: any; token: string }>('/auth/login', {
      method: 'POST',
      body: JSON.stringify(credentials),
    })

    if (response.success && response.data?.token) {
      this.setToken(response.data.token)
    }

    return response
  }

  // Trip methods
  async startTrip(startData: {
    start_latitude: number
    start_longitude: number
  }): Promise<ApiResponse<any>> {
    return this.request('/trips/start', {
      method: 'POST',
      body: JSON.stringify(startData),
    })
  }

  async stopTrip(tripData: {
    trip_id: number
    route_coordinates: Array<{
      latitude: number
      longitude: number
      speed: number
      timestamp: Date
    }>
  }): Promise<ApiResponse<any>> {
    return this.request('/trips/stop', {
      method: 'POST',
      body: JSON.stringify(tripData),
    })
  }

  async getTripHistory(page: number = 1, limit: number = 10): Promise<ApiResponse<{
    trips: any[]
    pagination: {
      currentPage: number
      totalPages: number
      totalTrips: number
      hasNextPage: boolean
      hasPrevPage: boolean
    }
  }>> {
    return this.request(`/trips/history?page=${page}&limit=${limit}`)
  }

  async getTripById(tripId: number): Promise<ApiResponse<any>> {
    return this.request(`/trips/${tripId}`)
  }

  // Token management
  setToken(token: string) {
    this.token = token
    if (typeof window !== 'undefined') {
      localStorage.setItem('speedchallenge_token', token)
    }
  }

  clearToken() {
    this.token = null
    if (typeof window !== 'undefined') {
      localStorage.removeItem('speedchallenge_token')
    }
  }

  getToken(): string | null {
    return this.token
  }

  isAuthenticated(): boolean {
    return !!this.token
  }
}

// Create and export a singleton instance
export const apiClient = new ApiClient(API_BASE_URL)

// Export types for use in components
export type { ApiResponse }
